<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input placeholder="" v-model="filters.account"></el-input>
        </el-form-item>
        <el-form-item label="" prop="startTime" :span="3">
          <el-date-picker type="datetime" v-model="filters.startTime" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="endTime" :span="3">
          <el-date-picker type="datetime" v-model="filters.endTime" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="filterData"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="topics" highlight-current-row v-loading="listLoading" style="width: 100%;">
      <el-table-column prop="account" label="" align="center" width="130">
      </el-table-column>
      <el-table-column prop="order_id" label="" align="center" min-width="185">
      </el-table-column>
      <el-table-column prop="cid" label="ID" align="center" width="130">
      </el-table-column>
      <el-table-column label="" align="center" width="130">
        <template slot-scope="scope">
          {{typeArr[scope.row.type]}}
        </template>
      </el-table-column>
      <el-table-column prop="sid" label="ID" align="center" width="130">
      </el-table-column>
      <el-table-column prop="order_time" label="" align="center" width="120">
      </el-table-column>
      <el-table-column prop="receving_time" label="" align="center" width="120">
      </el-table-column>
      <el-table-column prop="end_time" label="" align="center" width="120">
      </el-table-column>
      <el-table-column prop="reserve" label="" align="center" width="120">
      </el-table-column>
      <el-table-column prop="number" label="" align="center" width="100">
      </el-table-column>
      <el-table-column prop="user_coupon_id" label="ID" align="center" width="100">
      </el-table-column>
      <el-table-column prop="money" label="" align="center" width="100">
      </el-table-column>
      <el-table-column prop="user_coupon_money" label="" align="center" width="80">
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar" v-show="total > 0">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-count="totalPages" :current-page.sync="curtPage" :total="total" style="float:right;">
      </el-pagination>
    </el-col>

  </section>
</template>

<script>
import { getList } from 'api/cencus';
import Dayjs from 'dayjs';

export default {
  data() {
    return {
      filters: {
        account: '',
        startTime: Dayjs()
          .startOf('day')
          .format('YYYY-MM-DD HH:mm:ss'),
        endTime: Dayjs()
          .endOf('day')
          .format('YYYY-MM-DD HH:mm:ss'),
        page: '1'
      },
      typeArr: ['', '', '', '', ''],
      agents: [],
      roles: sessionStorage.getItem('rules'),
      topics: [],
      total: 0,
      curtPage: 1,
      totalPages: 1,
      listLoading: false,
      showFormVisible: false, // 
      showLoading: false
    };
  },
  methods: {
    getTopics(pages) {
      this.listLoading = true;
      this.filters.page = pages ? String(pages) : '1';
      let param = Object.assign({}, this.filters);
      let start = Dayjs(param.startTime).format('YYYY-MM-DD HH:mm:ss');
      let end = Dayjs(param.endTime).format('YYYY-MM-DD HH:mm:ss');
      getList({
        page: param.page,
        account: param.account,
        startTime: start,
        endTime: end
      }).then(res => {
        if (res.success) {
          this.setTopics(res.result, param.page);
        } else {
          this.$confirm(res.result.error, '', {
            type: 'warning'
          }).then(() => {
            // do...
          });
        }
        this.listLoading = false;
      });
    },
    setTopics(res, pages) {
      this.total = Number(res.totalData);
      this.curtPage = Number(pages);
      this.totalPages = Number(res.pages);
      this.topics = res.data;
    },
    handleCurrentChange(page) {
      this.getTopics(page);
    },
    filterData() {
      this.getTopics(1);
    }
  },
  mounted() {
    this.getTopics();
  }
};
</script>

<style scope lang="stylus">
.line {
  border-bottom: 1px solid #ccc;
  margin-bottom: 12px;
}
</style>



